import React, {PureComponent} from 'react'
import {
  Modal,
  Row,
  Col,
  Table,
  Button,
  message,
  Input,
  Select
} from 'antd'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {getNowPageId} from '../../global/components/menuData'
const confirm = Modal.confirm

@connect(
  state => ({...state.outWarehouseListSell, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {
      editAble: true
    }
    this.columns = [{
      title: '操作',
      width: 150,
      dataIndex: 'operation',
      key: 'operation',
      render: (text, record, index) => {
        const {editable} = record
        const {detailData} = this.props
        console.log(detailData, 33)
        console.log(editable)
        return (
          <div className="tableOperation">
            {
              !editable && detailData.status !== 8 && <span className='blue' onClick={() => this.edit(record.productCode)}>编辑</span>
            }
            {
              editable && detailData.status !== 8 && <span onClick={() => this.save(record.productCode)}>保存</span>
            }
          </div>
        )
      }
    }, {
      title: '商品编码',
      width: 150,
      dataIndex: 'productCode',
      key: 'productCode'
    }, {
      title: '商品名称',
      width: 150,
      dataIndex: 'productName',
      key: 'productName'
    }, {
      title: '销售单位',
      width: 100,
      dataIndex: 'unit',
      key: 'unit'
    }, {
      title: '出库数量',
      width: 100,
      dataIndex: 'planQty',
      key: 'planQty'
    }, {
      title: '签收数量',
      width: 100,
      dataIndex: 'signQty',
      key: 'signQty'
    }, {
      title: '差异数',
      width: 150,
      dataIndex: 'diffQty',
      key: 'diffQty',
      render: (text, record) => {
        const {editable} = record
        return (
              <Input disabled={!editable} value={record.diffQty} onChange={e => {
                record.diffQty = e.target.value
                this.forceUpdate()
              }}/>
        )
      }
    },
    {
      title: '差异原因',
      width: 150,
      dataIndex: 'remark',
      key: 'remark',
      render: (text, record) => {
        const {editable} = record
        const {handleShippedorders, ReasonKeyValue} = this.props
        handleShippedorders()
        return (
              <Select disabled={!editable} value = {record.remark} style={{width: 120}} onChange={(value) => {
                console.log('value is', value)
                console.log('record is', record)
                record.remark = value
                this.forceUpdate()
                console.log('record is 1222', record)
              }}>
                {
                  ReasonKeyValue.map((o, i) => (
                      <Select.Option key={i} value={o}>{o}</Select.Option>
                    ))
                }
              </Select>
        )
      }
    }, {
      title: '差异类型',
      width: 100,
      dataIndex: 'diffType',
      key: 'diffType'
    },
    {
      title: '可用库存',
      width: 100,
      dataIndex: 'availableQty',
      key: 'availableQty'
    }]
  }

  edit(key) {
    const {update, oldData} = this.props
    const newData = [...oldData]
    const target = newData.filter(item => key === item.productCode)[0]
    if (target) {
      target.editable = true

      update({oldData: newData})
    }
  }

  save(key) {
    const {oldData, handleUpdatediff, detailData} = this.props
    const newData = [...oldData]
    const target = newData.filter(item => key === item.productCode)[0]
    if (target) {
      // delete target.editable
      handleUpdatediff({data: {editable: target.editable, shippedOrderCode: detailData && detailData.shippedOrderCode, productCode: target.productCode, diffQty: target.diffQty, checkRemark: target.remark}}, () => {
        message.success('编辑成功')
        this._getAsyncData()
      }, () => {
        // this._getAsyncData()
      }
      )
    }
  }

  // edit(text, record) {
  //   const {editAble} = this.state
  //   if (editAble) {
  //     this.setState({editAble: !this.state.editAble})
  //   } else {
  //     const {detailData, handleUpdatediff} = this.props
  //     handleUpdatediff({data: {shippedOrderCode: detailData && detailData.shippedOrderCode, productCode: record && record.productCode, diffQty: record && record.diffQty, remark: record && record.remark}}, () => {
  //       this.setState({editAble: !this.state.editAble})
  //       this._getAsyncData()
  //     })
  //   }
  // }

  componentDidMount() {
    this._getAsyncData()
            //权限配置
    const {GetButtonPower} = this.props
    const MEUN = JSON.parse(localStorage.getItem('b2bUserMenu'))
    const ID = getNowPageId(MEUN, '/wms/out-warehouse/outWarehouseList/sell')
    console.log(ID, MEUN, 444)
    GetButtonPower({urlParams: [ID]})
  }
  // 根据tabs的key来判断请求哪个接口
  _getAsyncData() {
    const {handleCyorderPage, match: {params}} = this.props
    handleCyorderPage({searchParams: {orderCode: params.id}})
  }
  // 导出
  detailImport=() => {
    const {handleExportCyorder, match: {params}} = this.props
    handleExportCyorder({searchParams: {orderCode: params.id}})
  }

  //批量审核
  batchReview=() => {
    const {handleCyorDerchek, handleExportShortage, detailData} = this.props
    handleCyorDerchek({data: {shippedOrderCode: detailData.linkOrderCode}}, (data) => {
      if (data.result.hasError) {
        confirm({
          content: data.result.message,
          cancelText: '取消',
          okText: '缺货导出',
          onOk() {
            data.result.shortageList ?
            handleExportShortage({searchParams: {shortageList: data.result.shortageList}}) : message.error('暂无缺货导出')
          }
        })
      } else {
        message.success(data.result.message)
        this._getAsyncData()
      }
    })
  }

  // 关闭弹窗
  handleCloseModal = () => {
    const {history} = this.props
    history.length > 1 ? history.goBack() : window.close()
  }
  // 关闭弹窗
  handleClosemodifyModal = () => {
    this.setState({modifyModal: false, diffQty: ''})
  }
  render() {
    const {detailData, deliveryTableLoading, outWareAuditDifferences} = this.props
    const handleFont = {
      fontSize: '14px',
      marginBottom: '10px'
    }
    const spanStyle = {
      fontWeight: 'bold'
    }
    console.log('detailData is', detailData)
    return (
      <div className='fullOrderDel'>
        <span className='ant-modal-close-x close' onClick={this.handleCloseModal}></span>
        <div><h2>签收差异处理</h2></div>
        <Row>
          <Col span={12}><div style={handleFont}>出库单：<span style={spanStyle}>{detailData && detailData.shippedOrderCode}</span></div></Col>
          <Col span={12}><div style={handleFont}>差异单：<span style={spanStyle}>{detailData && detailData.linkOrderCode}</span></div></Col>
          <Col span={12}><div style={handleFont}>客户：<span style={spanStyle}>{detailData ? `${detailData.customerCode}-${detailData && detailData.customerName}` : ''}</span></div></Col>
          <Col span={12}><div style={handleFont}>配送地址：<span style={spanStyle}>{detailData && detailData.customerAddress}</span></div></Col>
          <Col span={12}><div style={handleFont}>签收人：<span style={spanStyle}>{detailData && detailData.updateBy}</span></div></Col>
          <Col span={12}><div style={handleFont}>签收时间：<span style={spanStyle}>{detailData && detailData.signDate}</span></div></Col>
          <Col span={12}><div style={handleFont}>审核人：<span style={spanStyle}>{detailData && detailData.checkBy}</span></div></Col>
          <Col span={12}><div style={handleFont}>审核时间：<span style={spanStyle}>{detailData && detailData.checkTime}</span></div></Col>
          <Col span={12}><div style={handleFont}>签收图片：
          {
            detailData && detailData.picUrl && detailData.picUrl.map(o => (
                <span key={o.indexOf(detailData.picUrl)} style={spanStyle}><a onClick={() => {
                  window.open(o)
                }}>查看图片{o.indexOf(detailData.picUrl) + 1}</a></span>
              ))
          }
          </div></Col>
          <Col span={12}><div style={handleFont}>备注：<span style={spanStyle}>{detailData && detailData.remark}</span></div></Col>
        </Row>
        <div>
          {
            outWareAuditDifferences && detailData && detailData.status == 7 && <Button type="primary" style={{marginRight: '15px', marginTop: '20px'}} onClick={this.batchReview}>审核差异</Button>
          }
          {
           detailData && detailData.status == 7 && <Button type="primary" onClick={this.detailImport}>导出</Button>
          }
        </div>
        <div className='table-group' style={{marginTop: '20px'}}>
          <Table
            rowKey={(record, index) => index}
            loading={deliveryTableLoading}
            columns={this.columns}
            scroll={{x: 'max-content', y: 400}}
            dataSource={detailData && detailData.items}
            pagination={false}
          />
        </div>
        <div>
          <Modal
            title="编辑商品差异数量"
            visible={this.state.modifyModal}
            onCancel={this.handleClosemodifyModal}
            // footer={true}
            mask={false}
            width={777}
            // okText="保存"
            // cancelText="取消"
            onOk={this.save}
          >
            <div>
              <div className='commonInput'>
                <label>差异数：</label>
                <div className='box'>
                  <Input value={this.state.diffQty ? this.state.diffQty : this.state.record && this.state.record.diffQty} onChange={(e) => {
                    this.setState({diffQty: e.target.value})
                  }}/>
              </div>
            </div>
            </div>
          </Modal>
        </div>
      </div>
    )
  }
}



